import Vue from 'vue'
import App from './App'
import router from './router'
import global_ from './pages/Global'
Vue.prototype.GLOBAL = global_;
// import bottom from '../components/bottom.vue'

Vue.config.productionTip = false;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



